<template>
    <!-- 发现 页面 -->
    <Layout>
        <template v-slot:header>
            <h1 class="find-title">发现</h1>
            <!-- 导航 -->
            <ul class="find-nav">
                <router-link class="find-nav-item"
                    tag="li"
                    active-class="find-nav-item--active"
                    :to="item.route"
                    v-for="(item, index) in findNavs" :key="index"
                >
                    <a>{{item.label}}</a>
                </router-link>
            </ul>
        </template>
        <div class="find">
            <!-- 把滚动条从 main 搬到这个页面来 -->
            <section class="find-content" @scroll="handelScoll" ref="findContent">
                <keep-alive :max="10">
                    <router-view ref="routerview"
                        :key="$route.fullPath"
                        :finds="findsFilter"
                        :columns="2"
                    ></router-view>
                </keep-alive>
            </section>
        </div>
    </Layout>
</template>

<script>
import Layout from '../layout/Layout.vue'
import Waterfall from '../base/Waterfall.vue'
import axios from 'axios'
import { mapState, mapGetters, mapMutations, mapActions } from 'vuex'

export default {
    name: 'Find',
    components: {
        Layout,
        Waterfall
    },
    data() {
        return {
            findNavs: [
                {
                    label: '推荐',
                    route: '/find/tuijian',
                    type: 'tuijian',
                },
                {
                    label: '旅行',
                    route: '/find/lvxing',
                    type: 'lvxing',
                },
                {
                    label: '丽人',
                    route: '/find/liren',
                    type: 'liren',
                },
                {
                    label: '电影',
                    route: '/find/dianying',
                    type: 'dianying',
                },
                {
                    label: '结婚',
                    route: '/find/jiehun',
                    type: 'jiehun',
                },
                {
                    label: '购物',
                    route: '/find/gouwu',
                    type: 'gouwu',
                },
                {
                    label: '家装',
                    route: '/find/jiazhuang',
                    type: 'jiazhuang',
                },
            ],
            scrollTopOfTypes: {},
        }
    },
    computed: {
        ...mapState('find', {
            type: state => state.type,
        }),
        ...mapGetters('find', [
            'findsFilter'
        ])
    },
    methods: {
        ...mapMutations('find', [
            'setType',
            'setObj'
        ]),
        ...mapActions('find', [
            'requestFinds',
            'loadMore'
        ]),
        _initScrollTopOfTypes(){
            this.findNavs.forEach((item => {
                this.scrollTopOfTypes[item.type] = 0
            }))
        },
        _initFinds(){
            let obj = {}
            this.findNavs.map(navItem => {
                return obj[navItem.type] = []
            })
            this.setObj(obj)
        },
        handelScoll(event){
            // 记录每个 type 的滚动距离
            this.scrollTopOfTypes[this.type] = event.target.scrollTop
            // 滚动条到底部时加载数据
            if(event.target.scrollTop + event.target.clientHeight === event.target.scrollHeight){
                this.loadMore()
            }
        }
    },
    created() {
        this._initFinds()
        this._initScrollTopOfTypes()
        this.requestFinds()
    },
    beforeRouteEnter(to,from,next){
        next(vm=>{
            // 进入 find 前, 重定向到之前退出的 type 路由, 如 '/find/tuijian'
            vm.$router.push(`/find/${vm.type}`); 
            // 滚动距离
            vm.$nextTick(() => {
                vm.$refs.findContent.scrollTop = vm.scrollTopOfTypes[vm.type]
            })
        })
    },
    beforeRouteUpdate (to, from, next) {
        // 切换时更新 type
        this.setType(to.params.type)
        // 切换时更新数据
        this.requestFinds()
        // 滚动距离
        this.$nextTick(() => {
            this.$refs.findContent.scrollTop = this.scrollTopOfTypes[this.type]
        })
        next()
    },
    beforeRouteLeave (to,from,next){
        // 离开 find 时保存当前 type
        this.setType(from.params.type)
        next();
    }
}
</script>

<style lang="less">
@import "~@/assets/css/mixin.less";

.find-title {
    text-align: center;
    line-height: 2em;
    .font-size-rem(@font-size-title,bold);
    color: @text-color-title;
    background-color: @bg-white;
}
.find-nav {
    background-color: @bg-white;
    .padding-horizontal(@padding-base);
    .padding-bottom-rem(@padding-base);
    .flex();
    overflow-x: scroll;
    &::-webkit-scrollbar {
        display: none;
    }
    &-item {
        position: relative;
        .font-size-rem(@font-size-base);
        .flex-shrink(0);
        .flex-basis(66px);
        text-align: center;
        line-height: 1.8em;
        &--active {
            color: @text-color-title;
            .font-size-rem(@font-size-root,bold);
            &:after {
                content: '';
                position: absolute;
                bottom: 0;
                left: 5px;
                width: 50px;
                height: 2px;
                background-color: @color-primary;
            }
        }
    }
}
.find{
    .padding-horizontal(@padding-base);
    height: 100%;
    &-content {
        height: 100%;
        overflow-y: scroll;
    }
}
</style>