<template>
    <ul class="waterfall" ref="waterfall">
        <li class="waterfall-item" v-for="(item,index) in items" :key="item.id" ref="waterfallitem" :style="{width: `${itemWidth}px`}">
            <slot class="waterfall-content">
                <div
                    :style="{height: `${item.height}px`,'background-color': item.bgcolor}"
                >
                    {{index}}
                </div>
            </slot>
        </li>
    </ul>
</template>

<script>
export default {
    name: 'Waterfall',
    props: {
        columns: {
            type: Number,
            default: 2
        },
        gap: {
            type: Number,
            default: 10,
        },
        itemHeight: {
            type: Number,
            default: 150,
        },
    },
    data() { 
        return {
            itemWidth: 0,
            items: [
                {
                    id: 1,
                    height: 110,
                    bgcolor: '#1d36c7',
                },
                {
                    id: 2,
                    height: 190,
                    bgcolor: '#1d76c7',
                },
                {
                    id: 3,
                    height: 150,
                    bgcolor: '#1d46c7',
                },
                {
                    id: 4,
                    height: 130,
                    bgcolor: '#1ff6c7',
                },
                {
                    id: 5,
                    height: 122,
                    bgcolor: '#2276c7',
                },
                {
                    id: 6,
                    height: 210,
                    bgcolor: '#3d76c7',
                },
                {
                    id: 7,
                    height: 110,
                    bgcolor: '#1d76c7',
                },
                {
                    id: 8,
                    height: 200,
                    bgcolor: '#3076c7',
                },
                {
                    id: 9,
                    height: 140,
                    bgcolor: '#1d7147',
                },
            ],
        }
    },
    methods: {
        waterFall(){
            // 原理: https://segmentfault.com/a/1190000012621936
            let elements = this.$refs.waterfallitem
            console.log(elements)
            let width = this.itemWidth
            let gap = this.gap
            let heightArr = []
            elements.forEach((ele,index)=>{
                if(index < this.columns){
                    // 第一行
                    ele.style.top = 0
                    ele.style.left = (width + gap) * index + 'px'
                    heightArr.push(ele.offsetHeight)
                }else{
                    // 其他行
                    let minHeight = Math.min(...heightArr)
                    let indexOfMin = heightArr.indexOf(minHeight)
                    ele.style.top = minHeight + gap  + 'px'
                    ele.style.left = (width + gap) * indexOfMin + 'px'
                    heightArr[indexOfMin] = minHeight + ele.offsetHeight + gap
                }
            })
        }
    },
    mounted(){
        this.$nextTick(()=>{
            let width = this.$refs.waterfall.offsetWidth
            this.itemWidth = (width - (this.columns - 1) * this.gap) / this.columns
            this.waterFall();
        })
    },
}
</script>

<style lang="less">
.waterfall{
    color: #fff;
        position: relative;
    .waterfall-item {
        position: absolute;
        .waterfall-content {
        }
    }
}
</style>